@import url("https://fonts.cdnfonts.com/css/pt-sans-2");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
/* font-family: 'Poppins', sans-serif; */
/* font-family: 'PT Sans', sans-serif; */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  padding-bottom: 3rem;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(26, 26, 26);
  overflow-y: scroll; /* Always show the scrollbar */
  scrollbar-width: thin; /* For Firefox */
  -ms-overflow-style: none; /* For Internet Explorer and Microsoft Edge */
}

/* For Internet Explorer and Microsoft Edge */


/* Define the custom scrollbar track */
body::-webkit-scrollbar {
  width: 4px;
  background-color: #000000;
}

body::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

/* Firefox */
body {
  scrollbar-width: thin;
  scrollbar-color: #888 #000000;
}

/* Edge */
body::-ms-scrollbar {
  width: 4px;
  background-color: #000000;
}

body::-ms-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

body::-ms-scrollbar-thumb:hover {
  background-color: #555;
}

/* Standard scrollbar (Internet Explorer 10+) */
body {
  scrollbar-width: thin;
  scrollbar-color: #888 #000000;
}
